<template>
   <div class="home" :key="keyComponent" v-if="showPage">
      <Row style="margin:20px" class="filter-nav">
         <Col span="4" style="padding-right:10px">
            <Select
               v-model="selectedFindBill"
               filterable
               clearable
               style="width:150px"
               placeholder="رقم الفاتورة"
               @on-change="findInvoiceByNo"
            >
               <Option
                  v-for="(bill, index) in allBills"
                  :value="bill.billNo"
                  :key="index"
                  >{{ bill.billNo }}</Option
               >
            </Select>
         </Col>
         <Col span="3" style="padding-right:10px">
            <Select
               v-model="selectedFindIncome"
               filterable
               clearable
               style="width:150px"
               placeholder="رقم الوصل"
               @on-change="findInvoiceByIncome"
            >
               <Option
                  v-for="(bill, index) in allBills"
                  :value="bill.invoiceNumber"
                  :key="index"
                  >{{ bill.invoiceNumber }}</Option
               >
            </Select>
         </Col>
         <Col span="3" style="padding-right:10px">
            <Select
               v-model="selectedFindStore"
               placeholder="اسم المحل او الصفحة"
               filterable
               clearable
               @on-clear="clearData"
               style="width:150px"
               @on-change="findInvoiceByStoreName"
            >
               <Option
                  v-for="(store, index) in allStores"
                  :value="store.idStore"
                  :key="index"
                  >{{ store.storeName }}</Option
               >
            </Select>
         </Col>
         <Col span="3">
            <Select
               v-model="selectedFindProvince"
               placeholder="المحافظة"
               filterable
               clearable
               @on-clear="clearData"
               style="width:150px"
               @on-change="findInvoiceByProvinceId"
            >
               <Option
                  v-for="(province, index) in allProvince"
                  :value="province.idProvince"
                  :key="index"
                  >{{ province.provinceName }}</Option
               >
            </Select>
         </Col>

         <Col span="4">
            <Select
               v-model="selectedFindDriver"
               placeholder="اسم السائق"
               filterable
               clearable
               @on-clear="clearData"
               style="width:150px"
               @on-change="findInvoiceByDriverId"
            >
               <Option
                  v-for="(driver, index) in allDrivers"
                  :value="driver.idDriver"
                  :key="index"
                  >{{ driver.driverName }}</Option
               >
            </Select>
         </Col>
         <Col span="3">
            <DatePicker
               v-model="invoiceDate"
               type="date"
               placeholder="اختر يوم"
               style="width: 150px"
               @on-clear="clearData"
               @on-change="findInvoiceByDate"
            ></DatePicker>
         </Col>

         <Col span="4">
            <Button
               type="success"
               shape="circle"
               icon="ios-add"
               @click="addInvoice = true"
               >اضافة فاتورة استلام</Button
            >
         </Col>
      </Row>

      <Table
         border
         :columns="columns12"
         :data="allBills"
         size="small"
         :loading="loading"
         class="table-income"
         style="direction:rtl;margin:10px"
      >
         <template slot-scope="{ row }" slot="name">
            <strong>{{ row.name }}</strong>
         </template>
         <template slot-scope="{ row, index }" slot="action">
            <Button
               type="info"
               shape="circle"
               icon="ios-information-circle"
               size="small"
               @click="show(index)"
            ></Button>
         </template>
         <template slot-scope="{ row, index }" slot="sequence">{{
            index + 1
         }}</template>
      </Table>

      <h3>المجموع : {{ getTotalPrice }} دينار</h3>

      <Modal
         v-model="addInvoice"
         title="اضافة فاتورة استلام"
         width="600px"
         @on-ok="ok"
         @on-cancel="cancel"
      >
         <Row style="direction:rtl;margin:10px auto">
            <Col span="12" style="text-align:center">
               <span> تاريخ الفاتورة : {{ getDate(new Date()) }} </span>
            </Col>
            <Col span="12" style="text-align:center">
               <span>رقم الفاتورة : {{ allAvailableBills.length + 1 }}</span>
            </Col>
         </Row>
         <Row style="direction:rtl">
            <Col span="12" style="text-align:center">
               <div>
                  <Input
                     v-model="customerPhone"
                     placeholder=" رقم الهاتف..."
                     style="width: 200px"
                  />
               </div>
               <div style="margin : 5px auto">
                  <Select
                     v-model="selectedAddProvince"
                     placeholder="المحافظة"
                     filterable
                     clearable
                     style="width:200px"
                  >
                     <Option
                        v-for="(province, index) in allProvince"
                        :value="province.idProvince"
                        :key="index"
                        >{{ province.provinceName }}</Option
                     >
                  </Select>
               </div>

               <div style="margin : 5px auto">
                  <Select
                     v-model="selectedAddDriver"
                     placeholder="اسم السائق"
                     filterable
                     clearable
                     style="width:200px"
                  >
                     <Option
                        v-for="(driver, index) in allDrivers"
                        :value="driver.idDriver"
                        :key="index"
                        >{{ driver.driverName }}</Option
                     >
                  </Select>
               </div>
               <div style="margin : 5px auto">
                  <Select
                     v-model="selectedAddStore"
                     placeholder="المحل او الصفحة"
                     filterable
                     clearable
                     style="width:200px"
                  >
                     <Option
                        v-for="(store, index) in allStores"
                        :value="store.idStore"
                        :key="index"
                        >{{ store.storeName }}</Option
                     >
                  </Select>
               </div>
            </Col>
            <Col span="12" style="text-align:center">
               <div>
                  <Input
                     v-model="customerName"
                     placeholder="اسم الزبون..."
                     style="width: 200px"
                  />
               </div>
               <div style="margin : 5px auto">
                  <Input
                     v-model="address"
                     placeholder="العنوان..."
                     style="width: 200px"
                  />
               </div>
               <div style="margin : 5px auto">
                  <Input
                     type="number"
                     v-model="totalPrice"
                     placeholder="السعر الكلي..."
                     style="width: 200px"
                  />
               </div>
               <div style="margin : 5px auto">
                  <Input
                     type="number"
                     v-model="invoiceNumber"
                     placeholder="رقم الوصل"
                     style="width: 200px"
                  />
               </div>
            </Col>
         </Row>
      </Modal>
   </div>
</template>

<script>
export default {
   name: "Home",
   data() {
      return {
         columns12: [
            {
               title: "ت",
               slot: "sequence",
               width: 70,
               align: "center",
            },
            {
               title: "رقم الفاتورة",
               key: "billNo",
            },
            {
               title: "اسم الزبون",
               key: "customerName",
            },
            {
               title: "عنوان الزبون",
               key: "address",
            },
            {
               title: "هاتف الزبون",
               key: "customerPhone",
            },
            {
               title: "اسم الصفحة/المحل",
               key: "storeName",
            },

            {
               title: "المحافظة",
               key: "provinceName",
            },
            {
               title: "السعر",
               key: "totalPrice",
            },
            {
               title: "رقم الوصل",
               key: "invoiceNumber",
            },
            {
               title: "اسم السائق",
               key: "driverName",
            },
            {
               title: "Action",
               slot: "action",
               width: 70,
               align: "center",
            },
         ],
         statusData: [
            { statusId: 1, statusName: "في المخزن" },
            { statusId: 2, statusName: "قيد التوصيل" },
            { statusId: 3, statusName: "تم التسليم" },
            { statusId: 4, statusName: "مسترجع" },
         ],
         allBills: [],
         editInvoiceData: "",
         showPage: false,
         allDrivers: [],
         allStores: [],
         originalBills: [],
         invoiceNumber: 0,
         originalDrivers: [],
         originalStores: [],
         allProvince: [],
         originalProvince: [],
         selectedAddProvince: "",
         deliveredStatus: false,
         waitToChangeStatus: false,
         editInvoice: false,
         rowToChangeStatus: {},
         selectedFindDriver: "",
         selectedFindIncome: "",
         selectedFindBill: "",
         selectedFindStatus: "",
         selectedFindProvince: "",
         selectedFindStore: "",
         customerName: "",
         totalPrice: "",
         customerPhone: "",
         address: "",
         invoiceDate: "",
         note: "",
         allAvailableBills: [],
         selectedStore: 0,
         addInvoice: false,
         loading: true,
         value: "",
         selectedDriver: 0,
         selectedAddDriver: 0,
         selectedAddStore: 0,
         keyComponent: 0,
         url: this.$store.state.url,
      };
   },
   created() {
      let self = this;
      self.getAllData();
   },
   methods: {
      getDate(datetime) {
         let date = new Date(datetime);
         return (
            date.getDate() +
            " / " +
            (date.getMonth() + 1) +
            " / " +
            date.getFullYear()
         );
      },

      confirmReturn() {
         let self = this;
         this.$http
            .put(`${self.url}/updateReturn/${self.rowToChangeStatus.idBill}`, {
               returned: 1,
            })
            .then((res) => {
               console.log(res.data);
               self.$Message["success"]({
                  background: true,
                  content: "تم تحديث الحالة الى استرجاع",
               });
               self.deliveredStatus = false;
               self.getAllData();
               self.keyComponent += 1;
            })
            .catch((e) => {
               console.log(e);
               self.$Message["error"]({
                  background: true,
                  content: "حدث خطأ اثناء تعديل الحالة تأكد من المعلومات",
               });
            });
      },
      confirmStatus() {
         let self = this;

         this.$http
            .put(
               `${self.url}/updateDelivered/${self.rowToChangeStatus.idBill}`,
               { delivered: 1 }
            )
            .then((res) => {
               console.log(res.data);
               self.$Message["success"]({
                  background: true,
                  content: "تم تحديث الحالة الى استلام",
               });
               self.deliveredStatus = false;
               self.getAllData();
               self.keyComponent += 1;
            })
            .catch((e) => {
               console.log(e);
               self.$Message["error"]({
                  background: true,
                  content: "حدث خطأ اثناء تعديل الحالة تأكد من المعلومات",
               });
            });
      },
      show(index) {
         let self = this;
         self.$Modal.info({
            title: "معلومات الفاتورة",
            content: `التاريخ：${self.getDate(
               self.allBills[index].takeDate
            )}<br>الملاحظات：${
               self.allBills[index].note
            }<br>هاتف المحل/الصفحة：${self.allBills[index].storePhone}`,
         });
      },
      showEdit(row) {
         let self = this;
         console.log(row);
         self.editInvoiceData = row;
         self.editInvoice = true;
      },

      getAllData() {
         let self = this;

         let province = `${self.url}/allProvince`;
         let bills = `${self.url}/getTakeBills`;
         let availableBill = `${self.url}/bills`;
         let stores = `${self.url}/allStores`;
         let drivers = `${self.url}/drivers`;

         function requestDrivers() {
            return self.$http.get(province);
         }

         function requestAvailableBills() {
            return self.$http.get(availableBill);
         }

         function requestDriver() {
            return self.$http.get(drivers);
         }

         function requestBills() {
            return self.$http.get(bills);
         }

         function requestStores() {
            return self.$http.get(stores);
         }

         Promise.allSettled([
            requestDrivers(),
            requestBills(),
            requestStores(),
            requestDriver(),
            requestAvailableBills(),
         ])
            .then((responses) => {
               const responseOne = responses[0];
               const responseTwo = responses[1];
               const responseThree = responses[2];
               const responseFour = responses[3];
               const responseFive = responses[4];
               console.log(responseOne);
               self.loading = false;

               self.allProvince =
                  responseOne.status == "fulfilled"
                     ? responseOne.value.data
                     : [];
               self.allAvailableBills =
                  responseFive.status == "fulfilled"
                     ? responseFive.value.data
                     : [];
               self.originalProvince =
                  responseOne.status == "fulfilled"
                     ? responseOne.value.data
                     : [];
               self.allBills =
                  responseTwo.status == "fulfilled"
                     ? responseTwo.value.data
                     : [];
               self.originalBills =
                  responseTwo.status == "fulfilled"
                     ? responseTwo.value.data
                     : [];
               self.allStores =
                  responseThree.status == "fulfilled"
                     ? responseThree.value.data
                     : [];
               self.originalStores =
                  responseThree.status == "fulfilled"
                     ? responseThree.value.data
                     : [];

               self.allDrivers =
                  responseFour.status == "fulfilled"
                     ? responseFour.value.data
                     : [];
               self.originalDrivers =
                  responseFour.status == "fulfilled"
                     ? responseFour.value.data
                     : [];

               self.showPage = true;

               console.log(self.allDrivers);
               console.log(self.allStores);
               console.log(self.allBills);
            })
            .catch((errors) => {
               console.log(errors);
            });
      },

      ok() {
         let self = this;

         if (
            self.selectedAddProvince &&
            self.selectedAddStore &&
            self.customerName &&
            self.customerPhone &&
            self.address &&
            self.totalPrice
         ) {
            if (self.customerPhone.length == 11) {
               let bill = {
                  customerName: self.customerName,
                  customerPhone: self.customerPhone,
                  address: self.address,
                  totalPrice: self.totalPrice,
                  note: self.note,
                  invoiceNumber: self.invoiceNumber,
                  customerName: self.customerName,
                  customerPhone: self.customerPhone,
                  address: self.address,
                  totalPrice: self.totalPrice,
                  note: "nothing",
                  billNo: self.editInvoiceData,
                  provinceId: self.selectedAddProvince,
                  storeId: self.selectedAddStore,
                  driverId: self.selectedAddDriver,
                  sendDriver: 0,
               };

               console.log(bill);
               this.$http
                  .post(`${self.url}/addTakeBill`, bill)
                  .then((res) => {
                     console.log(res.data);
                     self.$Message["success"]({
                        background: true,
                        content: "تم حفظ البيانات",
                     });
                     self.selectedAddProvince = "";
                     self.selectedAddStore = "";
                     self.customerName = "";
                     self.totalPrice = "";
                     self.customerPhone = "";
                     self.address = "";
                     self.invoiceDate = "";
                     self.note = "";
                     self.getAllData();
                     self.keyComponent += 1;
                  })
                  .catch((e) => {
                     console.log(e);
                     self.$Message["error"]({
                        background: true,
                        content: "حدث خطأ اثناء حفظ البيانات تأكد من المعلومات",
                     });
                  });
            } else {
               self.$Message["error"]({
                  background: true,
                  content: "رقم الهاتف غير صحيح يجب ان يحتوي على ١١ رقما",
               });
            }
         } else {
            self.$Message["error"]({
               background: true,
               content: "الرجاء ادخال جميع الحقول",
            });
         }
      },

      okEdit() {
         let self = this;

         let bill = self.editInvoiceData;

         console.log(bill);
         this.$http
            .post(`${self.url}/editBill/${self.editInvoiceData.idBill}`, bill)
            .then((res) => {
               console.log(res.data);
               self.$Message["success"]({
                  background: true,
                  content: "تم حفظ البيانات",
               });
               self.getAllData();
               self.keyComponent += 1;
            })
            .catch((e) => {
               console.log(e);
               self.$Message["error"]({
                  background: true,
                  content: "حدث خطأ اثناء حفظ البيانات تأكد من المعلومات",
               });
            });
      },
      cancel() {
         this.$Message.info("Clicked cancel");
      },
      findInvoiceByProvinceId() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.provinceId == self.selectedFindProvince;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByNo() {
         let self = this;

         let matchNumber;

         matchNumber = self.allBills.find((bill) => {
            return bill.billNo == self.selectedFindBill;
         });
         if (matchNumber) {
            self.allBills = [matchNumber];
         } else {
            self.allBills = self.originalBills;
         }
      },

      findInvoiceByStoreName() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.storeId == self.selectedFindStore;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByStatusId() {
         let self = this;
         let matchName = [];

         if (self.selectedFindStatus == 1) {
            matchName = self.allBills.filter((bill) => {
               return bill.pending == 0;
            });
         } else if (self.selectedFindStatus == 2) {
            matchName = self.allBills.filter((bill) => {
               return (
                  bill.pending == 1 && bill.delivered == 0 && bill.returned == 0
               );
            });
         } else if (self.selectedFindStatus == 3) {
            matchName = self.allBills.filter((bill) => {
               return (
                  bill.pending == 1 && bill.delivered == 1 && bill.returned == 0
               );
            });
         } else if (self.selectedFindStatus == 4) {
            matchName = self.allBills.filter((bill) => {
               return (
                  bill.pending == 1 && bill.delivered == 0 && bill.returned == 1
               );
            });
         } else {
            self.allBills = self.originalBills;
         }

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByDriverId() {
         let self = this;

         let matchName = self.allBills.filter((bill) => {
            return bill.driverId == self.selectedFindDriver;
         });

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByIncome() {
         let self = this;

         let matchNumber;

         matchNumber = self.allBills.find((bill) => {
            return bill.invoiceNumber == self.selectedFindIncome;
         });
         if (matchNumber) {
            self.allBills = [matchNumber];
         } else {
            self.allBills = self.originalBills;
         }
      },
      findInvoiceByDate() {
         let self = this;

         let utcDate = new Date(self.invoiceDate).toLocaleDateString();

         console.log(utcDate);

         let matchName = self.allBills.filter((bill) => {
            return new Date(bill.takeDate).toLocaleDateString() == utcDate;
         });

         console.log(matchName);

         if (matchName) {
            self.allBills = matchName;
         } else {
            self.invoiceDate = "";
            self.allBills = self.originalBills;
         }
      },
      clearData() {
         let self = this;
         self.invoiceDate = "";
         self.allBills = self.originalBills;

         self.keyComponent += 1;
      },
   },
   computed: {
      getTotalPrice: function() {
         let total = this.allBills.reduce(function(accumulator, currentValue) {
            return accumulator + currentValue.totalPrice;
         }, 0);

         return total;
      },
   },
};
</script>
<style lang="scss">
@media print {
   @page {
      margin: 5px;
   }
   #nav {
      display: none;
   }
   #print-btn {
      display: none;
   }
   body {
      -webkit-print-color-adjust: exact !important;
   }
   .filter-nav {
      display: none !important;
   }
   .table-income {
      width: 100% !important;
   }
}
</style>
